@import 'styles/_colors.scss';
@import 'styles/_mixin.scss';
@import 'styles/_breakpoints.scss';

.tableHeight {
  height: calc(100vh - 217px);
  @media (max-width: $md) {
    height: calc(100vh - 182px);
  }
}

.addBtnDiv {
  position: absolute;
  bottom: 10px;
  right: 10px;
  width: 174px;
  height: 42px;
  @media (max-width: $md) {
    display: none;
  }
  .addBtnChildDiv {
    position: fixed;
  }
}

.settingsMainPage {
  background-color: #fafafa;
  display: flex;
  margin-top: 10px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  flex-direction: column;
  margin-bottom: 20px;
  border-radius: 5px;
}

.modalWrapper {
  max-width: 930px !important;
  padding: 0px !important;
  @media (max-width: $xs) {
    height: 90vh;
  }
  .modal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .img {
      margin-right: 20px;
      margin-top: -25px;
    }
  }
  .add {
    @include gridTwo(8, 50);
    padding: 20px 50px;
    @media (max-width: $sm) {
      padding: 20px 20px;
    }
    .label {
      font-size: 16px;
      color: $header;
      text-transform: capitalize;
    }

    .radio {
      display: flex;
      align-items: center;
      margin-top: 7px;
      .sec {
        margin-left: 50px;
      }
    }
  }

  .webBtnDiv {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    margin-right: 10px;
    .btn {
      padding: 12px 100px;
      margin: 30px 0px 30px 0;
      width: 303px;
      height: 51px;
    }
  }

  .mobileBtnDiv {
    display: none;
    margin: 30px;
    @media (max-width: $md) {
      display: flex;
      justify-content: flex-end;
    }
    .mobileBtn {
      position: relative;
      left: 0;
      bottom: 0;
    }
  }
}

.loaderDiv {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  inset: 0;
  background-color: #fafafa;
  z-index: 200;
  .loadingStyle {
    width: 40px;
    height: 40px;
    border-width: 3px;
    border-color: #57b993;
    border-top-color: transparent;
  }
}
